import React from 'react'
import { Typography, useMediaQuery } from '@mui/material'
import OCListDescPopover, { HelpIcon } from './OCListDescPopover'
import { rankingArgDto } from '../config/config'

function ListDesc({ list, isAll, isSearch }: { list: ListParams['list']; isAll: boolean; isSearch: boolean }) {
  const p = { sx: { fontSize: 14 }, color: 'text.secondary' }

  switch (list) {
    case 'hourly':
      return (
        <div>
          {!isAll && (
            <Typography gutterBottom {...p}>
              10
            </Typography>
          )}
          {isAll && (
            <Typography gutterBottom {...p}>
              10
            </Typography>
          )}
          <Typography {...p}></Typography>
        </div>
      )
    case 'daily':
      return (
        <div>
          {!isAll && (
            <Typography gutterBottom {...p}>
              10
            </Typography>
          )}
          {isAll && (
            <Typography gutterBottom {...p}>
              10
            </Typography>
          )}
          <Typography {...p}></Typography>
        </div>
      )
    case 'weekly':
      return (
        <div>
          {!isAll && (
            <Typography gutterBottom {...p}>
              10
            </Typography>
          )}
          {isAll && (
            <Typography gutterBottom {...p}>
              10
            </Typography>
          )}
          <Typography {...p}></Typography>
        </div>
      )
    case 'all':
      return (
        <div>
          {!isAll && <Typography {...p}></Typography>}
          {isAll && <Typography {...p}></Typography>}
        </div>
      )
  }
}

function HelpButton({ list, cateIndex, isSearch }: { list: ListParams['list']; cateIndex: number; isSearch: boolean }) {
  return (
    <OCListDescPopover>
      <ListDesc list={list} isAll={cateIndex === 0} isSearch={isSearch} />
    </OCListDescPopover>
  )
}

const weekdays = ['', '', '', '', '', '', '']

export default function OCListTitleDesc({
  list,
  cateIndex,
  isSearch,
  visibility = true,
}: {
  list: ListParams['list']
  cateIndex: number
  isSearch: boolean
  visibility?: boolean
}) {
  const matches = useMediaQuery('(min-width:600px)') // 599px false
  const divCss: React.CSSProperties = { display: 'flex', flexWrap: 'wrap' }
  const outer: React.CSSProperties = { ...divCss, marginBottom: '.5rem' }
  const inner: React.CSSProperties = { ...divCss, gap: '4px' }
  const p = { fontWeight: 700, fontSize: matches ? '17px' : '15px' }

  const updatedAt = new Date(rankingArgDto.modifiedUpdatedAtDate.replaceAll("-","/"))
  const past = new Date(rankingArgDto.modifiedUpdatedAtDate.replaceAll("-","/"))
  const hourly = new Date(rankingArgDto.hourlyUpdatedAt.replaceAll('-', '/'))

  switch (list) {
    case 'hourly':
      return (
        <div style={outer}>
          <Typography children="" sx={{ ...p, mr: 1 }} />
          <div style={inner}>
            <Typography children={`${hourly.getHours()}:${hourly.getMinutes()}`} sx={p} />
            {visibility ? <HelpButton list={list} cateIndex={cateIndex} isSearch={isSearch} /> : <HelpIcon />}
          </div>
        </div>
      )
    case 'daily':
      past.setDate(past.getDate() - 1)
      return (
        <div style={outer}>
          <Typography children="" sx={{ ...p, mr: 1 }} />
          <div style={inner}>
            <Typography
              children={`${updatedAt.getMonth() + 1}${updatedAt.getDate()}(${weekdays[updatedAt.getDay()]})`}
              sx={p}
            />
            {visibility ? <HelpButton list={list} cateIndex={cateIndex} isSearch={isSearch} /> : <HelpIcon />}
          </div>
        </div>
      )
    case 'weekly':
      past.setDate(past.getDate() - 7)
      return (
        <div style={outer}>
          <Typography children="" sx={{ ...p, mr: 1 }} />
          <div style={inner}>
            <Typography
              children={`${past.getMonth() + 1}${past.getDate()}(${weekdays[past.getDay()]})${
                updatedAt.getMonth() + 1
              }${updatedAt.getDate()}(${weekdays[updatedAt.getDay()]})`}
              sx={p}
            />
            {visibility ? <HelpButton list={list} cateIndex={cateIndex} isSearch={isSearch} /> : <HelpIcon />}
          </div>
        </div>
      )
    case 'all':
      return (
        <div style={outer}>
          <div style={inner}>
            {!isSearch ? (
              <Typography children="" sx={p} />
            ) : (
              <Typography children="" sx={p} />
            )}
            {visibility ? <HelpButton list={list} cateIndex={cateIndex} isSearch={isSearch} /> : <HelpIcon />}
          </div>
        </div>
      )
  }
}
